import { useParams, useHistory } from "react-router-dom";
import { useTags } from "hooks/useTags";
import Layout from "components/Layout";
import Icon from "components/Icon";
import { Button } from "components/Button";
import styled from "styled-components";
import { Input } from "components/Input";
import { Center } from "components/Center";
import { Space } from "components/Space";
type Params = { tagId: string };
const Header = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px;
  font-size: 16px;
  background: white;
`;

const InputWrapper = styled.div`
  margin-top: 8px;
  padding: 0 0 0 15px;
  background: white;
`;

const EditTag: React.FunctionComponent = () => {
  const { tagId } = useParams<Params>();
  const { findTag, updateTag, deleteTag } = useTags();
  const tag = findTag(tagId);
  const onChangeHandle: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    if (tag) {
      updateTag(tag.id, e.target.value);
    }
  };
  const onClickHandle = () => {
    if (tag) {
      deleteTag(tag.id);
    }
  };
  const tagContent = (tag: { id: number; name: string }) => (
    <InputWrapper>
      <Input
        type="text"
        label=""
        value={tag.name}
        onChange={onChangeHandle}
      />
    </InputWrapper>
  );
  const history = useHistory();
  const onClickBack = () => {
    history.goBack();
  };
  return (
    <Layout>
      <Header>
        <Icon name="left" onClick={onClickBack} />
        <span></span>
        <Icon />
      </Header>
      {tag ? tagContent(tag) : <div></div>}
      <Center>
        <Space />
        <Button onClick={onClickHandle}></Button>
      </Center>
    </Layout>
  );
};

export { EditTag };
