import { useEffect, useRef } from "react";

const useUpdate = (fn: () => void, deps: any[]) => {
  //useRefrender
  const count = useRef(0);
  useEffect(() => {
    count.current++;
  });
  //render.undefined->[]
  useEffect(() => {
    if (count.current > 1) {
      fn();
    }
  }, deps);
};

export { useUpdate };
