import styled from "styled-components";

const Wrapper = styled.section`
  display: flex;
  flex-direction: column;
  .output {
    padding: 15px 10px;
    text-align: right;
    font-size: 36px;
    background: #ffffff;
    box-shadow: inset 0 -5px 3px -5px rgba(0, 0, 0, 0.25),
      inset 0 5px 3px -5px rgba(0, 0, 0, 0.25);
  }
  .number-buttons {
    &.clearfix::after {
      content: "";
      display: block;
      clear: both;
      height: 0;
      visibility: hidden;
    }
    > button {
      float: left;
      width: 25%;
      height: 64px;
      border: none;
      font-size: 18px;
      &.ok {
        float: right;
        height: 128px;
        background: #8b8b8b;
      }
      &.zero {
        width: 50%;
      }
      &:nth-child(1) {
        background: #f2f2f2;
      }
      &:nth-child(2),
      &:nth-child(5) {
        background: #e0e0e0;
      }
      &:nth-child(3),
      &:nth-child(6),
      &:nth-child(9) {
        background: #d3d3d3;
      }
      &:nth-child(4),
      &:nth-child(7),
      &:nth-child(10) {
        background: #c1c1c1;
      }
      &:nth-child(8),
      &:nth-child(11),
      &:nth-child(13) {
        background: #b8b8b8;
      }
      &:nth-child(14) {
        background: #9a9a9a;
      }
    }
  }
`;

export { Wrapper };
